@import url("https://fonts.googleapis.com/css?family=Raleway:400");

.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:flex-start;
    width:100%;
    height:auto;
    /* background-color:red; */
    gap: 3vw;
    color: white;
    /* font-family: 'Montserrat', sans-serif;
    font-family: 'Oswald', sans-serif; */
    /* text-shadow: 1.5px 1.5px 2.5px rgb(121, 121, 121); */
    padding: 0 5%;
    
   
}


.section1 {
    display:flex;
    justify-content: center;
    align-items: flex-start;
    gap: 2vw;
    flex-wrap: wrap;
}


.title {
    padding-top: 1vh;
}


h1 {
    text-shadow: 1.5px 1.5px 2.5px rgb(116, 109, 109);
}

.text1 {
    
    flex: 0.35; /* Take available space but can shrink or grow as per content */
    min-width: 10vw; /* A reasonable minimum width for text content */
}

.text1 p {
    color:grey;
}

.midImageContainer {
    
    /* max-width: 35%; */
    flex: 0.35; /* Take available space but can shrink or grow based on viewport */
    min-width: 7vw; /* A reasonable minimum width for the image */
   
}

.midImage {
    width: 100%;
    height: auto; /* Maintains the aspect ratio of the image */
    display: block; /* Removes any default alignments, making height adjustments accurate */
    
    
}

.section2 {
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    gap:1vw;
}

.firstLine, .secondLine {

    display:flex;
    justify-content: center;
    align-items: center;
   
}


.section3 {
    display:flex;
    align-items: center;
    margin: 5vw;
}

.text3 {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap:0.9em;
    
}

.text3 p {
    font-size: 1.2rem;
    color: grey
}

.teamPortraits {
    display:flex;
    gap:5vw;
    flex-wrap: wrap;
    
}

@media (max-width: 768px) {
    .section1, .section2, .section3 {
        margin: 5vw; /*Adjust the margin value as per your needs */
        
    }

    .section1 {
        flex-direction: column;
        align-items: center;
        flex-direction: column-reverse;
    }

    .firstLine, .secondLine {
        flex-wrap: wrap;
        flex-direction: column; 
        align-items: center; 
        
    }

    .teamPortraits {
        flex-direction: column;
        align-items: center;
        gap: 2vw; /* You can adjust the gap as per your design needs */
        background-color: black;
    }
}